import React, { useMemo, useState } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import type { Class, ClassSchedule, Course, User } from '../../types';
import { Calendar, Users, ChevronRight, ChevronDown, Search } from 'lucide-react';

const mockCourse = (id: string, name: string, code: string, credits = 3): Course => ({
  id,
  code,
  name,
  credits,
  semester: 5,
});

const mockLecturer = (id: string, name: string): User => ({
  id,
  name,
  email: `${name.split(' ').join('.').toLowerCase()}@university.ac.id`,
  role: 'lecturer',
  employeeId: `D${id}`,
});

const schedule = (day: ClassSchedule['day'], start: string, end: string, room: string): ClassSchedule => ({
  day,
  startTime: start,
  endTime: end,
  room,
});

const mockClasses: Class[] = [
  {
    id: 'CLS-101',
    courseId: 'CS101',
    course: mockCourse('CS101', 'Database Systems', 'CS101'),
    lecturerId: 'L1',
    lecturer: mockLecturer('1', 'Dr. Ahmad Rahman'),
    semester: '2024/2025 Genap',
    schedule: [schedule('Monday', '09:00', '11:00', 'A201')],
    capacity: 50,
    enrolled: 45,
    room: 'A201',
  },
  {
    id: 'CLS-202',
    courseId: 'CS202',
    course: mockCourse('CS202', 'Advanced Algorithms', 'CS202'),
    lecturerId: 'L1',
    lecturer: mockLecturer('1', 'Dr. Ahmad Rahman'),
    semester: '2024/2025 Genap',
    schedule: [schedule('Wednesday', '13:00', '15:00', 'B305')],
    capacity: 40,
    enrolled: 32,
    room: 'B305',
  },
];

const ClassManagement: React.FC = () => {
  const { user } = useAuth();
  const [expanded, setExpanded] = useState<string | null>(null);
  const [query, setQuery] = useState('');

  const classes = useMemo(() => {
    const lecId = user?.id || 'L1';
    return mockClasses.filter(c => c.lecturer.id === lecId);
  }, [user?.id]);

  const filtered = classes.filter(
    c => c.course.name.toLowerCase().includes(query.toLowerCase()) || c.course.code.toLowerCase().includes(query.toLowerCase())
  );

  return (
    <div className="max-w-7xl mx-auto">
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-2xl font-bold text-gray-900">Class Management</h1>
        <div className="text-sm text-gray-500">Active semester  2024/2025 Genap</div>
      </div>

      <div className="mb-4 relative">
        <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
        <input
          className="w-full pl-9 pr-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500"
          placeholder="Search by course code or name"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
        />
      </div>

      <div className="space-y-4">
        {filtered.map(cls => (
          <div key={cls.id} className="card">
            <div className="flex items-start justify-between">
              <div>
                <div className="font-semibold text-gray-900">{cls.course.name} ({cls.course.code})</div>
                <div className="text-sm text-gray-500">Credits: {cls.course.credits}  Capacity {cls.enrolled}/{cls.capacity}</div>
                <div className="flex items-center text-sm text-gray-600 mt-2">
                  <Calendar className="h-4 w-4 mr-2" />
                  {cls.schedule.map((s, i) => (
                    <span key={i} className="mr-3">{s.day} {s.startTime}-{s.endTime}  Room {s.room}</span>
                  ))}
                </div>
              </div>
              <button
                className="text-primary-600 hover:text-primary-700 flex items-center"
                onClick={() => setExpanded(prev => prev === cls.id ? null : cls.id)}
              >
                {expanded === cls.id ? (
                  <>
                    Hide details <ChevronDown className="h-4 w-4 ml-1" />
                  </>
                ) : (
                  <>
                    View details <ChevronRight className="h-4 w-4 ml-1" />
                  </>
                )}
              </button>
            </div>

            {expanded === cls.id && (
              <div className="mt-4 border-t pt-4">
                <div className="flex items-center mb-2">
                  <Users className="h-4 w-4 text-gray-500 mr-2" />
                  <span className="font-medium text-gray-800">Enrolled Students</span>
                </div>
                <div className="overflow-x-auto">
                  <table className="min-w-full text-sm">
                    <thead>
                      <tr className="text-left text-gray-500">
                        <th className="py-2">No</th>
                        <th className="py-2">Student ID</th>
                        <th className="py-2">Name</th>
                        <th className="py-2">Status</th>
                      </tr>
                    </thead>
                    <tbody>
                      {Array.from({ length: cls.enrolled }).slice(0, 10).map((_, i) => (
                        <tr key={i} className="border-t">
                          <td className="py-2">{i + 1}</td>
                          <td className="py-2">22010{i.toString().padStart(3, '0')}</td>
                          <td className="py-2">Student {i + 1}</td>
                          <td className="py-2">enrolled</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
};

export default ClassManagement;
