import React from 'react';
import { Users, BookOpen, Clock, Award } from 'lucide-react';

const LecturerDashboard: React.FC = () => {
  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Lecturer Dashboard</h1>
        <div className="text-sm text-gray-500">Academic Year 2024/2025</div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-primary-100 rounded-lg">
              <Users className="h-6 w-6 text-primary-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Total Students</div>
              <div className="text-2xl font-bold text-gray-900">150</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 rounded-lg">
              <BookOpen className="h-6 w-6 text-green-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Active Classes</div>
              <div className="text-2xl font-bold text-gray-900">5</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 rounded-lg">
              <Clock className="h-6 w-6 text-blue-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Teaching Hours</div>
              <div className="text-2xl font-bold text-gray-900">12</div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-yellow-100 rounded-lg">
              <Award className="h-6 w-6 text-yellow-600" />
            </div>
            <div className="ml-4">
              <div className="text-sm font-medium text-gray-500">Pending Grades</div>
              <div className="text-2xl font-bold text-gray-900">23</div>
            </div>
          </div>
        </div>
      </div>

      <div className="card">
        <h2 className="text-lg font-semibold text-gray-900 mb-4">Today's Classes</h2>
        <div className="space-y-3">
          <div className="flex items-center p-3 bg-gray-50 rounded-lg">
            <div className="flex-1">
              <div className="font-medium text-gray-900">Database Systems (Class A)</div>
              <div className="text-sm text-gray-500">Room A201  45 students</div>
            </div>
            <div className="text-sm font-medium text-gray-600">09:00 - 11:00</div>
          </div>
          <div className="flex items-center p-3 bg-gray-50 rounded-lg">
            <div className="flex-1">
              <div className="font-medium text-gray-900">Advanced Algorithms (Class B)</div>
              <div className="text-sm text-gray-500">Room B305  32 students</div>
            </div>
            <div className="text-sm font-medium text-gray-600">13:00 - 15:00</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LecturerDashboard;
